import React, { Component } from 'react';
import { connect} from 'react-redux'
import { createStore } from 'redux'
import { bindActionCreators } from 'redux'

import LoginRedux from '.././View/LoginRedux'
import {emailValidator, passwordValidator} from '../Servises/checker'
import Connector from '../View/Connector'

class LoginReduxContainer extends Component {
    render() {
        return  <Connector store={store}/>
    }
}

const initialState = {
    email: '',
    password: '',
    emailError: emailValidator(null),
    passwordError: passwordValidator(null),
    submitFailed: false
};

const reducer = (state = initialState, action) => {
      switch (action.type) {
        case 'EMAIL_CHANGE': 
            return {
                ...state,
                email: action.email,
                emailError: action.emailError 
        }
        case 'PASSWORD_CHANGE': 
            return {
                ...state,
                password: action.password,
                passwordError: action.passwordError 
        }
        case 'SUBMITTER': 
            var result;
            if (action.submitFailed === false) {
                result = {
                    email: action.email,
                    password: action.password,
                    emailError: action.emailError,
                    passwordError: action.passwordError,
                    submitFailed: action.submitFailed  
               }
            } else {
                result = {
                    ...state,
                    submitFailed: action.submitFailed
                }
            }
            return result;
        default: return {
            ...state  
        }
    } 
};

const store = createStore(reducer);

function emailChange (event) {
    return {
        type: 'EMAIL_CHANGE',
        email: event.target.value,
        emailError: emailValidator(event.target.value)
    };
};

function passwordChange (event) {
    return {
        type: 'PASSWORD_CHANGE',
        password: event.target.value,
        passwordError: passwordValidator(event.target.value)
    };
};

function submitter () {
    let state = store.getState();
    var result;
    if (!state.emailError && !state.passwordError && state.email && state.password) {
        console.log(`Email: ${state.email} Password: ${state.password}`);
        result = {
            type: 'SUBMITTER',
            email: '',
            password: '',
            emailError: emailValidator(null),
            passwordError: passwordValidator(null),
            submitFailed: false
        }
    } else {
        result = {
            type: 'SUBMITTER',
            submitFailed: true
        }
    }
    return result;
};

const mapStateToProps = (state) => {
    return {
        email: state.email,
        password: state.password,
        emailError: state.emailError,
        passwordError: state.passwordError,
        submitFailed: state.submitFailed
    };
};

const mapDispatchToProps = (dispatch) => {
    return {
        emailChange: bindActionCreators(emailChange, dispatch),
        passwordChange: bindActionCreators(passwordChange, dispatch),
        submitter: bindActionCreators(submitter, dispatch)
    };
};

const Container = connect(mapStateToProps, mapDispatchToProps)(LoginRedux);

export default LoginReduxContainer;
export {Container}


